import { Component, Input, OnDestroy, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';
import { IArticleClientDto } from '../api/article-client-dto';
import { DiHelpService } from '../api/di-help.service';

@Component({
    selector: 'di-help',
    templateUrl: './di-help.template.html',
    styleUrls: ['./di-help.style.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class DiHelpComponent implements OnDestroy {

    article: IArticleClientDto;

    private routerEventsSubscription: Subscription;
    private _articleId: number;
    private _articleUid: string;
    private _byLocation: boolean;

    @Input()
    get articleId(): number {
        return this._articleId;
    }
    set articleId(value: number) {
        if (this.byLocation) throw Error('Do not use article-id with by-location');
        if (this.articleUid) throw Error('Do not use article-id with article-uid');

        if (this._articleId === value) return;

        this._articleId = value;

        if (!value) return;
        this.diHelpService.getArticleById(value)
            .subscribe(article => {
                this.article = article;
                this.changeDetector.markForCheck();
            }, () => {
                this.article = null;
                this.changeDetector.markForCheck();
            });
    }

    @Input()
    get articleUid(): string {
        return this._articleUid;
    }
    set articleUid(value: string) {
        if (this.byLocation) throw Error('Do not use article-uid with by-location');
        if (this.articleId) throw Error('Do not use article-uid with article-id');

        if (this._articleUid === value) return;

        this._articleUid = value;

        if (!value) return;
        this.diHelpService.getArticleByUid(location.origin, value)
            .subscribe(article => {
                this.article = article;
                this.changeDetector.markForCheck();
            }, () => {
                this.article = null;
                this.changeDetector.markForCheck();
            });
    }

    @Input()
    get byLocation(): boolean {
        return this._byLocation;
    }
    set byLocation(value: boolean) {
        if (this.articleId) throw Error('Do not use by-location with article-id');
        if (this.articleUid) throw Error('Do not use by-location with article-uid');

        if (this._byLocation === value) return;

        this._byLocation = value;

        if (value) {
            this.updateFromLocation();
            this.routerEventsSubscription = this.router.events
                .pipe(filter(event => event instanceof NavigationEnd))
                .subscribe(() => {
                    this.updateFromLocation();
                });
        } else {
            if (this.routerEventsSubscription) {
                this.routerEventsSubscription.unsubscribe();
            }
        }
    }

    @Input()
    alwaysVisible: boolean = true;

    @Input()
    inline: boolean = false;

    constructor(
        private readonly diHelpService: DiHelpService,
        private readonly router: Router,
        private readonly changeDetector: ChangeDetectorRef
    ) { }

    ngOnDestroy(): void {
        if (this.routerEventsSubscription) {
            this.routerEventsSubscription.unsubscribe();
        }
    }

    private updateFromLocation(): void {
        this.diHelpService.getArticleByUrl(location.origin + location.pathname)
            .subscribe(article => {
                this.article = article;
                this.changeDetector.markForCheck();
            }, () => {
                this.article = null;
                this.changeDetector.markForCheck();
            });
    }
}
