import { Component, Input, OnInit } from '@angular/core';
import { IArticleClientDto } from '../api/article-client-dto';
import { DiHelpService } from '../api/di-help.service';

@Component({
    selector: 'di-help-faq',
    templateUrl: './di-help-faq.template.html'
})
export class DiHelpFaqComponent implements OnInit {

    articles: IArticleClientDto[];

    private _siteId: number;
    private allArticles: IArticleClientDto[];

    @Input()
    get siteId(): number {
        return this._siteId;
    }
    set siteId(value: number) {
        if (this._siteId === value) return;

        this._siteId = value;

        if (!value) return;
        this.diHelpService.getFaqBySiteId(value).subscribe(articles => {
            this.allArticles = articles;
            this.applyFilter();
        }, () => {
            this.allArticles = null;
            this.applyFilter();
        });
    }

    constructor(private readonly diHelpService: DiHelpService) { }

    ngOnInit(): void {
        if (!this.siteId) {
            this.diHelpService.getFaqBySiteUrl(location.origin).subscribe(articles => {
                this.allArticles = articles;
                this.applyFilter();
            }, () => {
                this.allArticles = null;
                this.applyFilter();
            });
        }
    }

    applyFilter(filter: string = '') {
        if (filter) {
            const query = filter.toLowerCase();
            this.articles = this.allArticles.filter(a =>
                a.Name.toLowerCase().includes(query)
                || a.ShortText.toLowerCase().includes(query)
                || (a.DiHelpFiles && a.DiHelpFiles.some(f => f.Name.toLowerCase().includes(query)))
                || (a.Links && a.Links.some(l => l.Name.toLowerCase().includes(query))));
        } else {
            this.articles = this.allArticles;
        }
    }
}
