import { Provider, InjectionToken } from '@angular/core';
import { Observable } from 'rxjs';

export abstract class DiHelpUrlResolver {
    abstract resolve(): string | Observable<string>;
}

export interface DiHelpConfig {
    diHelpUrl?: string;
    diHelpUrlResolver?: Provider;
    cacheLifetimeSecond?: number,
}

export const DI_HELP_URL_TOKEN = new InjectionToken<string>('DI_HELP_URL');
export const DI_HELP_CACHE_LIFETIME_TOKEN = new InjectionToken<string>('DI_HELP_CACHE_LIFETIME');
