import { Injectable, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of, throwError, isObservable } from 'rxjs';
import { map, catchError, tap, share, finalize, flatMap } from 'rxjs/operators';
import { ArticleClientDto } from './article-client-dto';
import { DiHelpUrlResolver, DI_HELP_URL_TOKEN, DI_HELP_CACHE_LIFETIME_TOKEN } from '../di-help.config';

interface ICacheEntry {
    article?: ArticleClientDto;
    error?: any;
}

export const cacheLifetimeSecondDefault = 30 * 60;

@Injectable()
export class DiHelpService {

    private readonly cacheLifetime: number;

    private readonly cacheById: { [id: number]: ICacheEntry } = {};
    private readonly requestCacheById: { [id: number]: Observable<ArticleClientDto> } = {};
    private readonly cacheByUid: { [uid: string]: ICacheEntry } = {};
    private readonly requestCacheByUid: { [uid: string]: Observable<ArticleClientDto> } = {};
    private readonly cacheByUrl: { [url: string]: ICacheEntry } = {};
    private readonly requestCacheByUrl: { [url: string]: Observable<ArticleClientDto> } = {};

    private readonly diHelpUrl: Observable<string>;

    constructor(
        diHelpUrlResolver: DiHelpUrlResolver,
        @Inject(DI_HELP_URL_TOKEN) diHelpUrl: string,
        @Inject(DI_HELP_CACHE_LIFETIME_TOKEN) cacheLifetimeSecond: number,
        private readonly http: HttpClient,
    ) {
        if (diHelpUrlResolver) {
            const resolved = diHelpUrlResolver.resolve();
            if (typeof resolved === 'string') {
                this.diHelpUrl = of(resolved);
            } else if (isObservable(resolved)) {
                this.diHelpUrl = resolved;
            } else {
                throw Error('Not supported diHelpUrlResolver');
            }
        } else if (diHelpUrl) {
            this.diHelpUrl = of(diHelpUrl);
        } else {
            throw Error('Please, configure diHelpUrl or diHelpUrlResolver');
        }

        this.cacheLifetime = cacheLifetimeSecond * 1000;
    }

    getArticleById(id: number): Observable<ArticleClientDto> {
        return this.diHelpUrl.pipe(flatMap(baseAddress => this.cachedGet(
            this.cacheById,
            this.requestCacheById,
            id,
            `${baseAddress}/api/Articles/GetArticleById/${id}`)));
    }

    getArticleByUrl(url: string): Observable<ArticleClientDto> {
        return this.diHelpUrl.pipe(flatMap(baseAddress => this.cachedGet(
            this.cacheByUrl,
            this.requestCacheByUrl,
            url,
            `${baseAddress}/api/Articles/GetArticleByUrl/?url=${encodeURIComponent(url)}`)));
    }

    getArticleByUid(siteUrl: string, uid: string): Observable<ArticleClientDto> {
        return this.diHelpUrl.pipe(flatMap(baseAddress => this.cachedGet(
            this.cacheByUid,
            this.requestCacheByUid,
            uid,
            `${baseAddress}/api/Articles/GetArticleByUid/?siteUrl=${encodeURIComponent(siteUrl)}&uid=${encodeURIComponent(uid)}`)));
    }

    getFaqBySiteId(id: number): Observable<ArticleClientDto[]> {
        return this.diHelpUrl.pipe(flatMap(baseAddress => this.http
            .get(`${baseAddress}/api/Articles/GetFaqBySiteId/${id}`, {
                headers: { ignoreLoadingBar: '' }
            })
            .pipe(map(a => a as ArticleClientDto[]))));
    }

    getFaqBySiteUrl(siteUrl: string): Observable<ArticleClientDto[]> {
        return this.diHelpUrl.pipe(flatMap(baseAddress => this.http
            .get(`${baseAddress}/api/Articles/GetFaqBySiteUrl/?siteUrl=${encodeURIComponent(siteUrl)}`, {
                headers: { ignoreLoadingBar: '' }
            })
            .pipe(map(a => a as ArticleClientDto[]))));
    }

    private cachedGet(
        cache: { [id: number]: ICacheEntry } | { [url: string]: ICacheEntry },
        requestCache: { [id: number]: Observable<ArticleClientDto> } | { [id: number]: Observable<ArticleClientDto> },
        key: number | string,
        httpUrl: string
    ): Observable<ArticleClientDto> {
        if (cache[key]) {
            return cache[key].error ? throwError(cache[key].error) : of(cache[key].article);
        }

        if (requestCache[key]) return requestCache[key];

        requestCache[key] = this.http
            .get(httpUrl, {
                headers: { ignoreLoadingBar: '' }
            })
            .pipe(
                map(a => a as ArticleClientDto),
                tap(a => {
                    delete requestCache[key];
                    cache[key] = {
                        article: a,
                    };
                }),
                catchError(e => {
                    delete requestCache[key];
                    cache[key] = {
                        error: e,
                    };
                    return throwError(e);
                }),
                finalize(() => {
                    setTimeout(() => delete cache[key], this.cacheLifetime);
                }),
                share());

        return requestCache[key];
    }
}
